import React from 'react';
import Box from './box';

const BoxContainer = () => {
  // Define an array of emojis
  const emojis = ['🎾', '🍳', '📖', '🏊', '♟️', '🪵'];

  return (
    <div className='boxcontainer'>
      {emojis.map((emoji, index) => (
        <Box key={index} emoji={emoji} />
      ))}
    </div>
  );
}

export default BoxContainer;
